<template>
  <transition name="fade">
    <div id="totop" class="totop" v-if="showToTopButton" @click="toTop">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="48"
        height="48"
        viewBox="0 0 24 24"
        fill="none"
        stroke-width="3"
        stroke-linecap="square"
        stroke-linejoin="arcs"
      >
        <path d="M18 15l-6-6-6 6" />
      </svg>
    </div>
  </transition>
</template>

<script>
// This component expects that the mother has a div with id="top" and a div with id="card0"

export default {
  props: {
    waitTime: {
      type: Number,
      default: 1000,
    },
  },

  emits: ["totop"],

  data() {
    return {
      waitTimeCorrected: 1000,
      runningInterval: 0,

      initialTop: 0,
      showToTopButton: false,
    };
  },

  methods: {
    toTop: function () {
      // 1st method works in case the scroll bar is visible
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
      // 2nd method works in case the scroll bar is not visible
      document.getElementById("top").scrollTo(0, 0);
    },

    isScrolledUp(el) {
      const rect = el.getBoundingClientRect();
      const elemTop = rect.top;

      if (this.initialTop === 0) {
        this.initialTop = rect.top;
      }
      this.showToTopButton = this.initialTop - elemTop > 300;
      // console.log(this.initialTop, elemTop, this.showToTopButton);
    },

    checkIfShowMoreIsNecessary() {
      const that = this;
      that.runningInterval = window.setInterval(function () {
        // console.log("checking");

        const divFound = document.querySelector("#card0");

        if (divFound) {
          that.isScrolledUp(divFound);
        }
      }, this.waitTimeCorrected);
    },
  },

  mounted: function () {
    this.waitTimeCorrected = Number(this.waitTime);
    if (this.waitTimeCorrected < 500 || Number.isNaN(this.waitTimeCorrected)) {
      this.waitTimeCorrected = 500;
    }
    console.log(this.waitTimeCorrected);

    this.checkIfShowMoreIsNecessary();
  },

  beforeUnmount: function () {
    // console.log("clearing");
    window.clearInterval(this.runningInterval);
  },
};
</script>


<style lang="scss" scoped>
.totop {
  position: fixed;
  right: 10px;
  bottom: 10px;
  stroke: var(--main);
  background: var(--secBg);
  border: 2px solid var(--main);
  box-shadow: 0px 0px 23px var(--mainShadow);
  backdrop-filter: blur(35px);
  border-radius: 48px;
  z-index: 999;
  cursor: pointer;

  &:hover {
    transform: scale(1.05);
  }
}
</style>