<template>
  <input
    type="checkbox"
    @click="(state = !state), state === true ? $emit('on') : $emit('off')"
    class="tgl-flip tgl"
    id="toggle"
  />
  <label class="tgl-btn" :on="on" :off="off" for="toggle"></label>
</template>

<script>
export default {
  props: ["on", "off"],
  emits: ["on", "off"],

  data() {
    return {
      state: false,
    };
  },
};
</script>

<style lang="scss" scoped>
