    }

    &:after {
      content: attr(on);
      background: var(--main);
      transform: rotateY(-180deg);
    }

    &:before {
      background: var(--btnBg);
      content: attr(off);
    }

    &:active:before {
      transform: rotateY(-20deg);
    }
  }

  &:checked + .tgl-btn {
    &:before {
      transform: rotateY(180deg);
    }

    &:after {
      transform: rotateY(0);
      left: 0;
      background: var(--main);
    }

    &:active:after {
      transform: rotateY(20deg);
    }
  }
}
